import { Component } from "react";
import axios from "axios";
import LoadingImage from "../assets/loading.svg";

let endpoint = "https://yerba-mate-home.herokuapp.com";

export default class DatabasePage extends Component {
  state = {
    items: []
  }

  componentDidMount() {
    this.getAllMate();
  }

  // Getting all the mate from DB
  getAllMate = () => {
    axios.get(endpoint + "/api/mate")
      .then((response) => {
        if(response.data) {
          this.setState({
            items: response.data.map((item) => {
              return(
                <li className="database--item" key={item.slug}>
                  {item.name} - {item.country}<br />
                </li>
              );
            })
          });
        }
        else {
          this.setState({
            items: []
          });
        }
      });
  }

  render() {
    return(
      <div>
        <h1 className="database--title">Yerba Mate Database</h1>
        <ul className="database--list">
          {
            this.state.items.length == 0 ?
            <div className="database__loading">
              <img className="database__loading--img" src={LoadingImage} />
            </div>
            : this.state.items
          }
        </ul>
      </div>
    );
  }
}
